#particles canvas {
  z-index: -1;
  height: 100vh;
  width: 100vw;
  position: absolute;
}

.glassBox {
  background: rgba(255, 255, 255, 0.13);
  border-width: 0;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

@keyframes fall {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fall {
  animation: fall 0.5s ease-out;
}
